/* @import url('https://fonts.cdnfonts.com/css/helvetica-neue-5'); */
@import url('https://cdn.privex.io/fonts/helvetica-neue/standard/');

#bedbible_calculator {
	padding-top: 80px;
	padding-bottom: 80px;
	margin-top: 20px;
	margin-bottom: 20px;
	font-family: 'Helvetica Neue', sans-serif;
	background-color: #fcd5ce;
	color: #1d1d1f;
	font-weight: 400;
	font-size: 20px;
    border: 1px solid #ff4d54;
    border-radius: 5px;
    -webkit-box-shadow: -2px 9px 32px 4px rgba(0,0,0,0.53);
	-moz-box-shadow: -2px 9px 32px 4px rgba(0,0,0,0.53);
	box-shadow: -2px 9px 32px 4px rgba(0,0,0,0.53);
}

#bedbible_calculator h1 {
	font-size: 35px;	
}

#bedbible_calculator [type=number] {
	width: 100px;
}

#bedbible_calculator #submit_btn {
	background: #ff4d54;
    border-radius: 10px;
    border: 1px solid #ff4d54;
    color: #fff;
    cursor: pointer;
    font-size: 22px;
    line-height: 40px;
	padding-left: 35px;
    padding-right: 35px;
	margin-bottom: 15px;
}
#bedbible_calculator #submit_btn:hover {
	background: #fcd5ce;
	color: #ff4d54;
}

#input_block {
	padding-left: 20px;
	padding-right: 20px;
}

/* Result Block */

#chart-container {
	font-weight: 600;
}

#back_btn {
	background: #ff4d54;
    border-radius: 10px;
    border: 1px solid #ff4d54;
    color: #fff;
    cursor: pointer;
    font-size: 22px;
	padding-left: 35px;
    padding-right: 35px;
}

#lied {
	display: block;
}
#again {
	display: inline-block;
	font-size: 14px;
}

#peers {
	background-color: rgb(168, 239, 61);
	font-family: 'Helvetica Neue', sans-serif;
	margin-bottom: 20px;
	padding: 20px;
	position: relative;
    top: -12px;
	font-weight: 500;
}

#peers > b {
	font-weight: 800;
	font-size: 26px;
}

#bedbible_calculator label {}

#bedbible_calculator button {}

#bedbible_calculator input {}

#bedbible_calculator [type=number],
#bedbible_calculator [type=text],
#bedbible_calculator [type=select] {}

/*HIGHCHARTS*/
#bedbible_calculator rect {
  fill: #f7e9e6;
  padding-top: 20px;
}

/*END highcharts*/


/* Remove arrows */
/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

/* Solid Social Share Buttons */

.fluid {
	padding-top: 20px;
}

.btn-social,
.btn-social:visited,
.btn-social:focus,
.btn-social:hover,
.btn-social:active {
  color: #ffffff;
  text-decoration: none;
  transition: opacity .15s ease-in-out;
}

.btn-social:hover,
.btn-social:active {
 	opacity: .75;
}

.btn-fb,
.btn-fb:hover  {
  background-color: #3b5998;
}

.btn-tw,
.btn-tw:hover {
  background-color: #000000;
}

.btn-email,
.btn-email:hover {
	background-color: grey;
}
/* Fluid Styles */

.fluid {
	display: flex;
  }
  
  .fluid a {
	flex-grow: 1;
	margin-right: 0.25rem;
  }
  
  .fluid a:last-child {
	margin-right: 0rem;
  }

/* MEDIA QUERY */
/* LAPTOP */
@media (min-width: 1400px) {}

/* small LAPTOP */
@media (max-width: 1024px){
}

/* TABLET */
@media (max-width: 768px){
}

/* MOBILE */
@media (max-width: 475px) {

	#bedbible_calculator h1 {
		font-size: 30px;	
	}

	#bedbible_calculator [type=number] {
    	width: 80px;
	}
	#bedbible_calculator #submit_btn {
		margin-bottom: 0;
	}
	#bedbible_calculator [type=number] {
		width: 90px;
	}
	#bedbible_calculator {
		padding-top: 40px;
		padding-bottom: 40px;
	}
	#input_block {
		padding-left:  10px;
		padding-right: 10px;
	}
	#peers > b {
		font-size: 23px;
	}
}

@media (max-width: 375px) {
	#input_block {
		padding-left:  0;
		padding-right: 0;
	}
}

@media (max-width: 320px) {
	#bedbible_calculator [type=number] {
		width: 83px;
	}
}



